let leftsearchSchema = [
    {
    fieldName: "APPROVAL CHAIN",
    fieldType: "text",
    fieldIcon: "fa fa-chain",
    paramName:"chainName",
    fieldSearchConditionName: [''],
    value: null,
    listOptions: []
}, 
{
    fieldName: 'APPROVAL GROUP',
    fieldType: "ddl-check",
    fieldIcon: "fa fa-check-circle",
    paramName:"approvalGroupIds",
    fieldSearchConditionName: [''],
    isSearch: false,
    value:"",
    listOptions: []
},
 {
    fieldName: 'User',
    fieldType: "ddl-check",
    fieldIcon: "fa fa-users",
    paramName:"userIds",
    fieldSearchConditionName: [''],
    isSearch: false,
    value: "",
    listOptions: []
},
{
    fieldName: 'STATUS',
    fieldType: "ddl-check",
    fieldIcon: "fa fa-bar-chart",
    paramName:"statusIds",
    fieldSearchConditionName: [''],
    isSearch: false,
    value: "",
    listOptions: [
        {value:'1001',name:'ACTIVE'},
    {value:'1002',name:'INACTIVE'}
    ]
}, 
]


 function  bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    if (filtername == 'APPROVALGROUP') {
        DataService.GetApprovalGroupByCompanyId().then(response=>{    
            var json= response.data;
            if(json !=null){   
                json.forEach(function (item, index){
                    let obj={
                        name:item.APPROVAL_GROUP_NAME,
                        value:`${item.APPROVAL_GROUP_ID}`
                    }     
                    vm.leftSearchFields[1].listOptions.push(obj); 
                });            
                return vm.LeadStatusList;           
            }
        });
    }
    if (filtername == 'APPROVALRULE') {
        var url=`isIncludeLoginUserId=1&approvalGroupIds=&approvalChainIds=&moduleName=${filtername}&DepartmentIds`; 
    }
    DataService.LoadUsersByCompanyId(url).then(response=>{     
        var json= response.data;
        if(json !=null){   
            json.forEach(function (item, index){
                let obj={
                    name:item.USERNAME,
                    value:`${item.USER_ID}`
                }     
                vm.leftSearchFields[2].listOptions.push(obj); 
            });            
            return vm.LeadStatusList;           
        }
    });

}

export default { buttons,leftsearchSchema,bindleftCommonSearchdropdown};

