<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="true" :listType="['List']" ModuleName="Approval"
            SubModuleCode="Approval" IdentityColumn="APPROVALCHAINID" :HeaderText="$t('Approval Chain')"
            :ListData="ChainData" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS'">
                    <template v-if="data.row.SCOUNT == 0 && data.row.DCOUNT == 0">
                        <td class="text-center single-action" :title="data.row.STATUSID == 1001 ? 'Active' : 'Inactive'">
                            <div class="cstm-drop-btn">
                                <select v-model="data.row.STATUSID"
                                    @change="DropdownUpdateStatus(data.row.STATUSID, data.row.APPROVALCHAINID);"
                                    :class="data.row.STATUSID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                    <option value="1001" selected="selected">Active</option>
                                    <option value="1002">Inactive</option>
                                </select>
                            </div>
                        </td>
                    </template>

                    <template v-else>
                        <td class="text-center single-action" :title="data.row.STATUSID == 1001 ? 'Active' : 'Inactive'">
                            <div class="cstm-drop-btn">
                                <select v-model="data.row.STATUSID" disabled
                                    :class="data.row.STATUSID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                    <option value="1001" selected="selected">Active</option>
                                    <option value="1002">Inactive</option>
                                </select>
                            </div>
                        </td>
                    </template>

                </template>
                <template v-if="data.column.COLUMN_NAME == 'APPROVALCHAINNAME'">
                    <span class="text-dark" v-if="data.row.APPROVALCHAINNAME">
                        <a href="javascript:;" v-on:click="EditChain(data.row.APPROVALCHAINID)"
                            :id="data.row.APPROVALCHAINID"><em class="text-primary" aria-hidden="true">{{
                                data.row.APPROVALCHAINNAME
                            }}</em></a>
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ACTION'">
                    <td class="text-center single-action">
                        <div class="cstm-drop-btn">
                            <span><a v-on:click="ChangeOrder(data.row.APPROVALCHAINID)"><em class="text-primary"
                                        aria-hidden="true"><i class="fa fa-retweet text-dark action-icon">
                                        </i></em></a></span>
                        </div>
                    </td>
                </template>
            </template>
        </tg-list>
    </section>
</template>

<script>
import DataService from '../../service/DataService';
import Schema from '../ApprovalChain/ApprovalChainSchema.js'
export default {

    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            userIds: '',
            statusIds: '',
            approvalGroupIds: '',
            PageSize: 20,
            PageNumber: 1,
            TotalRecords: 0,
            SortBy: '',
            SortExp: '',
            isLoading: false,
            noRecord: true,
            CurrentPage: 1,
            TotalPages: 0,
            approvalChainListData: [],
            Headers: [{
                "COLUMN_NAME": 'APPROVALCHAINNAME',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Approval Chain Name',
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": 'GROUPS',
                "DATA_TYPE": "text",
                "SORTABLE": false,

                "VISIBLE": true,
                "DISPLAY_NAME": 'Approval Groups',
                "DISPLAY_ORDER": 2
            },

            {
                "COLUMN_NAME": 'USERS',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Users',
                "DISPLAY_ORDER": 3
            },

            {
                "COLUMN_NAME": 'DESCRIPTION',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Description',
                "DISPLAY_ORDER": 4
            },

            {
                "COLUMN_NAME": 'STATUS',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Status',
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": 'ACTION',
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Action',
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true,
                }
            },

            ],
            search: '',
            isPartial: 0,
            ChainData: []
        }
    },

    created() {
        this.FetchData();
        Schema.bindleftCommonSearchdropdown(this, 'Approval', 'APPROVALGROUP');
        Schema.bindleftCommonSearchdropdown(this, 'Approval', 'APPROVALRULE');
    },
    methods: {

        async FetchData() {
            var vm = this;
            var url = `${vm.searchCondition}&userIds=${vm.userIds}&approvalGroupIds=${vm.approvalGroupIds}&statusIds=${vm.statusIds}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&isPartial=${vm.isPartial}`;
            await DataService.ApprovalChainListing(url).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.ChainData = response.data;
                        vm.ChainData.forEach((row) => {
                            row.isCheckBoxDisabled = false;
                            if (row.DCOUNT == 0 && row.SCOUNT == 0) {
                                row.isCheckBoxDisabled = false;
                            } else {
                                row.isCheckBoxDisabled = true;
                            }
                        })
                        vm.TotalRecords = response.data[0].TOTALRECORDS
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;

                    } else {
                        vm.ChainData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.ChainData = [];
                    vm.TotalRecords = 0
                }
                setTimeout(function () {
                    vm.CheckBoxBootstrap();
                }, 100);
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata');
                }, 500);
            });
            vm.isLoading = false;

        },
        DeleteApprovalChain() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  "), function () {
                    vm.isLoading = true;
                    var url = `ids=${val}`
                    DataService.DeleteApprovalChain(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('ApprovalChain')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('ApprovalChain')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                    });
                });
            }
        },
        DropdownUpdateStatus(statusId, id) {
            var vm = this;
            vm.isLoading = true;
            vm.confirmR(vm.$t('Are you sure you want to update this record?'), true, false, function (result) {
                var url = `id=${id}&statusId=${statusId}&tablename=TALYGEN_APPROVAL_CHAIN&columnname=APPROVAL_CHAIN_ID`;
                DataService.UpdateStatus(url).then(response => {
                    var status = ''
                    vm.ShowAlert(vm.$t('UpdateStatus'), "success", ('Alert'));
                    vm.FetchData();
                    vm.isLoading = false;
                })
            })
            vm.FetchData();
            vm.isLoading = false;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Delete":
                    this.DeleteApprovalChain()
                    break;
                case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break;
                case "AddNewChain":
                    this.AddNewChain();
                    break;
            }
        },
        AddNewChain: function () {
            this.$router.push({
                name: 'Add Approval Chain',
            });
        },
        EditChain: function (APPROVALCHAINID) {
            this.$router.push({
                name: 'Add Approval Chain',
                params: {
                    id: APPROVALCHAINID
                }
            });
        },
        ChangeOrder: function (APPROVALCHAINID) {
            this.$router.push({
                name: 'Manage Group',
                params: {
                    id: APPROVALCHAINID
                }
            });
        },
        leftCommonSearch: function (value) {
            debugger
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        renderActions() { },

    },

}
</script>

<style></style>
